body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.orangeTheme {
  border: 1px solid #ef5a35;
  color: #ef5a35;
  background-color: white;
  overflow: hidden; }

.orangeTheme:hover {
  color: #fff;
  background-color: #ef5a35; }

.orangeTheme:focus {
  outline: none; }

.solid.orangeTheme {
  background-color: #ef5a35;
  color: #fff; }

@media only screen and (min-width: 500px) {
  .text-centered {
    margin-left: 20%; } }

@media only screen and (min-width: 960px) {
  .text-centered {
    margin-left: 30%; } }

.enterprise-box {
  padding-top: 7em;
  font-weight: 400;
  width: 100%;
  height: 100%;
  padding-top: 50%;
  font-size: 1.5em; }

.body-container {
  margin-top: -20px; }

.tab:hover {
  background-color: #5a6f90;
  color: #fff; }

.rounded-border {
  border-radius: 20px; }

.price-card {
  margin: "0px 100px 0px 10px"; }

