.orangeTheme {
  border: 1px solid #ef5a35;
  color: #ef5a35;
  background-color: white;
  overflow: hidden;
}

.orangeTheme:hover {
  color: #fff;
  background-color: #ef5a35;
}

.orangeTheme:focus {
  outline: none;
}

.solid.orangeTheme {
  background-color: #ef5a35;
  color: #fff;
}
